.contactsList {
  display: flex;
  flex-direction: column;
  row-gap: 12px;
  padding: 0;
  align-items: center;
  max-height: 250px;
  overflow-y: auto;
}

.contactsListBtn {
  margin-left: auto;
  border-color: transparent;
  background-color: transparent;
}

.listItem {
  display: flex;
  width: 100%;
  align-items: center;
  border-bottom: 1px solid #7fcbea;
}
